.inputBlock {
    margin-bottom: 20px;
}
.inputBlock > label {
    display: block;
    font-size: 12px;
    line-height: 16px;
    text-transform: uppercase;
    cursor: pointer;
    color: rgb(185,187,190);
}

.inputWrapper {
    font-size: 16px;
    width: 100%;
    border-radius: 3px;
    margin-top: 8px;
}

.inputWrapper input {
    padding: 10px;
    height: 40px;
    font-size: 16px;
    width: 100%;
    border-radius: 3px;
    color: hsl(210, calc(1 * 2.9%), 86.7%);
    border: none;
    background: rgb(32,34,37);
}